*, 
*::before, 
*::after {
	padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
    background-color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    line-height: 23px;
    color: #777;
    position: relative;
    overflow-x: hidden;
    text-align: center;
    width: 100vw;
}

p {
  font-size: 2.2rem;
  font-weight: 300;
}

a.link {
  color: #fff;
  cursor: pointer;
}

a.link:hover {
  border-bottom: 1pc solid white;
}