*, 
*::before, 
*::after {
	padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
    background-color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    line-height: 23px;
    color: #777;
    position: relative;
    overflow-x: hidden;
    text-align: center;
    width: 100vw;
}

p {
  font-size: 2.2rem;
  font-weight: 300;
}

a.link {
  color: #fff;
  cursor: pointer;
}

a.link:hover {
  border-bottom: 1pc solid white;
}
.HeadingOne {
  font-family: "Open Sans", sans-serif;
	-webkit-font-smoothing: antialiased;
	color: #999;
	font-size: 4rem;
	font-weight: 200;
	line-height: 1.4;
	margin: 0 0 10px;
	letter-spacing: 3px;
	text-transform: uppercase;
  
}

@media (max-width: 1000px) {
  .HeadingOne {
    font-size: 3.5rem;
  }
}

@media (max-width: 815px) {
  .HeadingOne {
    font-size: 3rem;
  }
}
  
@media (max-width: 636px) {
  .HeadingOne {
    font-size: 2.5rem;
  }
}
  
@media (max-width: 570px) {
  .HeadingOne {
    font-size: 2rem;
  }
}
  
@media (max-width: 430px) {
  .HeadingOne {
    font-size: 1.8rem;
  }
}
